@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap);
*{font-family:"Montserrat",sans-serif}.card-input{padding:1px;border-radius:4px;border:1px solid #636363;background-color:#636363}.card-input_header{height:30px;color:white;display:flex;justify-content:center;align-items:center;font-weight:400;font-size:0.8em}.card-input_body{display:flex;justify-content:center;align-items:center}.card-input_body input{width:100%;height:30px;border-radius:4px;border:1px solid #636363;outline-color:#ef6d34;text-align:center;font-weight:400;font-size:0.8em}.card-view-item{border-radius:6px;border:2px solid #A0A0A0;padding:15px;margin:0 0 10px}.card-view-item_title{margin:0 0 10px;font-weight:700;font-size:1em}.card-view-item_sub-title{margin:0 0 10px;font-weight:600;font-size:.9em}.card-view-item_dates-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(90px, 1fr));grid-template-rows:repeat(1, 1fr);grid-column-gap:5px;grid-row-gap:5px;margin:0 0 10px}.card-view-item_inputs-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(130px, 1fr));grid-template-rows:1fr;grid-column-gap:5px;grid-row-gap:5px}

